<template>
    <div>
        
    </div>
</template>
<style>
.login-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
}
</style>

<script>
    export default {
        data() {
            return {
                connect_count:0
            };
        },
        methods: {
            init(){
                setTimeout(() => {
                    this.$Spin.show({
                        render: (h) => {
                            return h('div', [
                                h('Icon', {
                                    'class': 'login-spin-icon-load',
                                    props: {
                                        type: 'load-c',
                                        size: 22
                                    }
                                }),
                                h('div', this.$t('page_connet.spin.logining'))
                            ])
                        }
                    });
                    this.setIntervalConnect()
                },500);
            },
            setIntervalConnect()
            {
                if(this.interval_connect=='')
                {
                    this.interval_connect=setInterval(() => {
                        if(this.connect_count>4)
                        {
                            clearInterval(this.interval_connect);
                            alert(this.$t('page_connet.alert.hub_connet_timeout'));
                        }
                        if(this.$store.state.wallet.comm.connected)
                        {
                            clearInterval(this.interval_connect);
                            this.$Spin.hide(); 
                            this.$router.push({
                                name: 'index'
                            });
                        }
                        else{
                            console.log('wait connect')
                            this.connect_count++;
                        }
                    }, 2000);
                }
            }

        },
        mounted() {
            this.init();
        },
        destroyed() {
            clearInterval(this.interval_connect);
        },
        computed: {
            interval_connect: {
                set (val) {
                    this.$store.state.interval.connect = val;
                },
                get() {
                    return this.$store.state.interval.connect;
                }
            },
        },
    };
</script>


